import React, { Component } from 'react';
import { withStyles, Frame } from 'arwes';
import AutosizeInput from 'react-input-autosize';


class InputQuery extends Component {

    state = { text: '' }
    
    constructor(props) {
        super(props);
    }

    render() {
        const { text } = this.state;
        return (
            <Frame
                animate={true}
                corners={4}
                layer='primary'
            >
                <div className={this.props.classes.wrapper}>
                    <AutosizeInput className={this.props.classes.font}
                    value={text}
                    placeholder="В чем смысл жизни?  "
                    onChange={event => this.setState({ text: event.target.value })}
                    />
                </div>
            </Frame>
        );
    }
}

export default withStyles((theme) => ({
    font: {
        color: 'black',
        fontFamily: 'caveat',
        fontStyle: 'oblique',
        backgroundColor: theme.background.primary.level0
    },
    wrapper: {
        float: 'right',
        backgroundColor: theme.background.primary.level0,
        width: '100%',
        padding: '0 0'
    }
}))(InputQuery);