import React, { Component } from 'react';
import { ThemeProvider, createTheme, Arwes, Line, List, Words, Loading, Row, Col, withStyles } from 'arwes';
import InputQuery from './InputQuery';
import SearchButton from './SearchButton';
import Input from './Input';
// import { Query } from './Query';

const API_ADDRESS = 'https://spotify-api-wrapper.appspot.com';

const Item = withStyles(theme => ({
    root: {
        marginBottom: 20,
        width: '100%',
        height: 20,
        transition: `all ${theme.animTime}ms ease-out`,
        fontSize: 20,
        fontWeight: 'bold',
        textAlign: 'center',
        lineHeight: 1,
        color: '#fff',
        background: '#0d94fe',
        [`@media (min-width: ${theme.responsive.small + 1}px)`]: {
            background: '#00be02'
        },
        [`@media (min-width: ${theme.responsive.medium + 1}px)`]: {
            background: '#8f009c'
        },
        [`@media (min-width: ${theme.responsive.large + 1}px)`]: {
            background: '#ba7b01'
        },
    },
}))(({ classes, n }) => (
    <div className={classes.root}>{n}</div>
));

class App extends Component {

    state = {
        query: '',
        author: null,
        items: []
    };

    onQueryClick = event => {
        console.log('event ', event);
    }

    onSearch = () => {
        fetch(`${API_ADDRESS}/artist/${this.state.query}`)
        .then(response => response.json())
        .then(json => {
            console.log('json', json);
            if (json.artists.total > 0) {
                const author = json.artists.items[0];
                this.setState({ author });

                fetch(`${API_ADDRESS}/artist/${author.id}/top-tracks`)
                .then(response => response.json())
                .then(json => this.setState({ items: json.tracks}))
                .catch(error => alert(error.message));
            }
        })
        .catch(error => alert(error.message));
    }

    onEnter = event => {
        if (event.key === 'Enter') {
            this.onSearch();
        }
    };

    render() {
        console.log('this.state', this.state);
        return (
            <ThemeProvider theme={createTheme()}>
                <Arwes>
                    <h2>JUST an ATOM</h2>
                    <div>
                        <Line animate/>
                    </div>
                    <Row>
                        <Col noMargin noGutter s={3} m={3}>
                            <List node='ul'>
                                <li></li>
                            </List>
                        </Col>
                        <Col noMargin noGutter s={7} m={7}>
                            <InputQuery />
                        </Col>
                        <Col noMargin noGutter s={2} m={2}>
                            <SearchButton />
                        </Col>
                    </Row>
                    <div>
                        <Line animate/>
                    </div>
                    <div>
                        <Loading animate />
                    </div>
                    <div>
                        <Line animate/>
                    </div>
                    <div>
                        <Row col s={6} m={4} offset={['s3', 'm4']}>
                            <Item n={1} />
                        </Row>
                        <Row>
                            <Col s={12} m={6} l={4} xl={3}><Item n={2} /></Col>
                            <Col s={12} m={6} l={4} xl={3}><Item n={3} /></Col>
                            <Col s={12} m={6} l={4} xl={3}><Item n={4} /></Col>
                            <Col s={12} m={6} l={4} xl={3} offset={['l4']}><Item n={5} /></Col>
                        </Row>
                        <Row>
                            <Col s={12}>
                                <Row nested noMargin>
                                    <Col s={6}><Item n={6} /></Col>
                                    <Col s={6}><Item n={7} /></Col>
                                </Row>
                            </Col>
                        </Row>
                    </div>
                </Arwes>
            </ThemeProvider>
        );
    }
}

export default App;